.navbarcontainer {
  height: 70px;
  width: auto;
  display: flex;
  background-color: #0f1624;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 99;
}
.logo {
  width: 30%;
  display: flex;
  justify-content: flex-end;
}
.logo h3 {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  display: flex;
  justify-content: flex-end;
  width: 100%;
  color: rgb(167, 228, 192);
}
.headerInfo {
  width: 40%;
  display: flex;
  justify-content: center;
}

.headerInfo ul {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  text-decoration: none;
}
.navbar-li {
  text-decoration: none;
  color: rgba(167, 228, 205, 0.692);
  padding-right: 20px;
}
.navbar-li:hover {
  color: rgb(167, 228, 205);
}
.navbar-li li {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14px;
  position: relative;
}
.navbar-li li::before {
  content: "";
  background-color: rgb(167, 228, 192);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3px;
  width: 0%;
  bottom: -10px;
  transition: all 0.5s ease;
}
.navbar-li li:hover::before {
  content: "";
  background-color: rgb(168, 248, 201);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3px;
  width: 100%;
  margin-top: 20px;
  transition: all 0.5s ease;
}
.socialmedialogo {
  width: 30%;
  display: flex;
  justify-content: flex-start;
}
.navbar-li.a.active {
  color: rgb(241, 240, 178);
}
.sociallogo-navlink {
  height: 20px;
  width: 30px;
  align-items: center;
  justify-content: flex-start;
  margin-right: 30px;
}
.sociallogo {
  margin-right: 30px;
  font-size: 20px;
  color: rgba(167, 228, 192, 0.685);
  transition: font-size 0.3s ease;
}
.sociallogo:hover {
  font-size: 25px;
  color: rgb(184, 248, 211);
  transition: font-size 0.3s ease;
}
@media (max-width: 900px) {
  .sociallogo {
    font-size: 25px;
    color: rgb(167, 228, 192);
  }
}

.hambager {
  color: rgb(167, 228, 192);
}
@media (min-width: 900px) {
  .hambager {
    display: none;
  }
}
@media (max-width: 900px) {
  .headerInfo {
    display: none;
  }
  .socialmedialogo {
    display: none;
  }
  .logo {
    width: 80%;
    font-size: 20px;
    display: flex;
    justify-content: flex-start;
    padding-left: 20px;
  }
  .logo h3 {
    width: 80%;
    font-size: 20px;
    display: flex;
    justify-content: flex-start;
    padding-left: 20px;
  }
  .hambager {
    width: 20%;
    display: flex;
    justify-content: flex-end;
    padding-right: 20px;
  }
  .navbar-li li {
    font-size: 18px;
  }
  .headerInfo ul {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease;
    margin-top: 50px;
  }
  .headerInfo-isopen ul {
    padding-top: 70px;
  }
  .headerInfo ul li {
    display: flex;
    height: 40px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease;
  }

  .headerInfo {
    background-color: #0f1624;
    width: 100%;
    position: absolute;
    top: -225px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease;
  }
  .headerInfo-isopen {
    background-color: #0e3853;
    width: 100%;
    min-height: 200px;
    position: absolute;
    top: 0px;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: top 0.5s ease;
  }
  .headerInfo-isopen li {
    list-style: none;
  }
}
